import { useEffect } from "react";
import { ToadScheduler, SimpleIntervalJob, Task } from 'toad-scheduler';

const scheduler = new ToadScheduler();

const task = new Task('reset scores', () => {
	let obj = [{ username: 'John', score: 15}]
  localStorage.setItem('testData', JSON.stringify(obj));
});

const resetScore = new SimpleIntervalJob(
	{ days: 1},
	task,
	'id_1'
);

scheduler.addSimpleIntervalJob(resetScore);

var submitted = false

const ScoreBoard = ({score, moves}) => {
  var data = localStorage.getItem('testData')
  if(!localStorage.getItem('testData')) {
    let obj = [{ username: 'John', score: 15}]
    localStorage.setItem('testData', JSON.stringify(obj));
  }
  const getData = async () => {
    data = localStorage.getItem('testData')
    data = JSON.parse(data)
    
  }

  const saveData = async () => {
    if(!submitted) {
      data = localStorage.getItem('testData')
      data = JSON.parse(data)
      let obj = { username: document.getElementById('username').value, score: score}
      data.push(obj)
      localStorage.setItem('testData', JSON.stringify(data));

      submitted = true
    }
  }

  useEffect(() => {
    getData()
  }, [])

  const descendingScore = JSON.parse(data)?.sort((a, b) => b.score - a.score)

  return (
      <div className="score-board">
          <h3 className="values" style={{'display': 'inline-block'}}>Score: {score}</h3>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<h3 className="values" style={{'display': 'inline-block'}}>Moves: {moves}</h3>
          <br />
          <input type="text" id="username" className="username" placeholder="Your username.."></input> <button onClick={saveData}>Submit</button>
          <br /><br /><br />
          <table id="scores">
          <h3 className="values" style={{'display': 'inline-block'}}>Today's High Scores</h3>
          {descendingScore?.map((gameState, index) => (
              <tbody key={index}>
                { index<1 && <tr><th>Username</th><th>Score</th></tr> }
                { index<10 && <tr><td>{gameState.username}</td><td>{gameState.score}</td></tr> }
              </tbody>  
          ))}
          </table>
      </div>
  )
}

export default ScoreBoard;